/* أنيميشن التحميل التكنولوجي */
@keyframes techPulse {
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.1); opacity: 0.7; }
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes charWave {
  0%, 100% { transform: translateY(0); opacity: 1; }
  50% { transform: translateY(-5px); opacity: 0.7; }
}

.tech-loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0a0a12;
  z-index: 9999;
}

.tech-spinner {
  position: relative;
  width: 80px;
  height: 80px;
  margin-bottom: 30px;
}

.tech-spinner .circle {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 3px solid transparent;
  mix-blend-mode: screen;
}

.tech-spinner .circle:nth-child(1) {
  border-top-color: #4f46e5;
  animation: rotate 1.5s linear infinite;
}

.tech-spinner .circle:nth-child(2) {
  border-top-color: #10b981;
  animation: rotate 2s linear infinite reverse;
}

.tech-spinner .circle:nth-child(3) {
  border-top-color: #f59e0b;
  animation: rotate 2.5s linear infinite;
}

.tech-spinner .circle:nth-child(4) {
  border-top-color: #ec4899;
  animation: rotate 3s linear infinite reverse;
}

.loading-text {
  display: flex;
  font-size: 1.5rem;
  color: #325be2;
  font-weight: 200;
  letter-spacing: 5px;
}

.loading-char {
  animation: charWave 1.4s ease-in-out infinite;
}

.loading-char:nth-child(1) { animation-delay: 0.1s; }
.loading-char:nth-child(2) { animation-delay: 0.2s; }
.loading-char:nth-child(3) { animation-delay: 0.3s; }
.loading-char:nth-child(4) { animation-delay: 0.4s; }
.loading-char:nth-child(5) { animation-delay: 0.5s; }
.loading-char:nth-child(6) { animation-delay: 0.6s; }
.loading-char:nth-child(7) { animation-delay: 0.7s; }
.loading-char:nth-child(8) { animation-delay: 0.8s; }
.loading-char:nth-child(9) { animation-delay: 0.9s; }
.loading-char:nth-child(10) { animation-delay: 1.0s; }
.loading-char:nth-child(11) { animation-delay: 1.1s; }
.loading-char:nth-child(12) { animation-delay: 1.2s; }
.loading-char:nth-child(13) { animation-delay: 1.3s; }
.loading-char:nth-child(14) { animation-delay: 1.4s; }

/* التنسيقات العامة */


body {
  background-color: #0a0a12;
  color: #e5e7eb;
  overflow-x: hidden;
}

.tech-app {
  position: relative;
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
  direction: rtl;
  opacity: 0;
  animation: fadeIn 0.8s ease-out forwards;
}

.tech-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%);
  z-index: -2;
}

.grid-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(79, 70, 229, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(79, 70, 229, 0.1) 1px, transparent 1px);
  background-size: 40px 40px;
  z-index: -1;
  opacity: 0.6;
}

.tech-glow {
  position: fixed;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(79, 70, 229, 0.4) 0%, rgba(79, 70, 229, 0) 70%);
  filter: blur(30px);
  z-index: -1;
  animation: float 6s ease-in-out infinite;
}

/* اا */
.tech-header {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 0 20px;
  margin-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.tech-header-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%);
  z-index: -3;
}

.tech-header-grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(79, 70, 229, 0.15) 1px, transparent 1px),
    linear-gradient(90deg, rgba(79, 70, 229, 0.15) 1px, transparent 1px);
  background-size: 50px 50px;
  opacity: 0.4;
}

.tech-header-glow {
  position: absolute;
  width: 60%;
  height: 60%;
  background: radial-gradient(circle, rgba(79, 70, 229, 0.3) 0%, transparent 60%);
  filter: blur(80px);
  animation: glowPulse 8s infinite alternate;
}

.tech-header-particles {
  position: absolute;
  width: 100%;
  height: 100%;
}

.tech-header-particle {
  position: absolute;
  background-color: rgba(165, 180, 252, 0.6);
  border-radius: 50%;
  width: var(--size);
  height: var(--size);
  left: var(--x);
  top: var(--y);
  animation: particleFloat var(--duration) infinite ease-in-out var(--delay);
  filter: blur(1px);
}

.tech-header-content {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  transform: translateY(-50px);
}

.tech-logo {
  position: relative;
  margin-bottom: 20px;
  display: inline-block;
}

.tech-logo h1 {
  position: relative;
  font-size: 4rem;
  background: linear-gradient(90deg, #e5e7eb 0%, #a5b4fc 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 800;
  letter-spacing: -2px;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.tech-logo-highlight {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(79, 70, 229, 0.3) 0%, transparent 100%);
  filter: blur(15px);
  z-index: -1;
  opacity: 0.7;
}

.tech-logo-line {
  height: 4px;
  width: 150px;
  background: linear-gradient(90deg, #4f46e5 0%, transparent 100%);
  margin: 10px auto 0;
  border-radius: 3px;
  animation: lineGrow 1.5s ease-out;
}

.tech-subtitle {
  color: #a5b4fc;
  font-size: 1.5rem;
  opacity: 0.9;
  margin-bottom: 40px;
  line-height: 1.5;
  text-shadow: 0 2px 10px rgba(79, 70, 229, 0.3);
}

.tech-header-stats {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin: 60px 0;
  flex-wrap: wrap;
}

.tech-stat {
  background: rgba(17, 17, 27, 0.6);
  border: 1px solid rgba(79, 70, 229, 0.2);
  border-radius: 12px;
  padding: 20px 30px;
  backdrop-filter: blur(5px);
  min-width: 150px;
  transition:  0.5s;
}

.tech-stat:hover {
  transform: translateY(-15px);
  scale: 1.3;
  z-index: 99;
  box-shadow: 0 10px 20px rgba(79, 70, 229, 0.2);
  border-color: rgba(79, 70, 229, 0.4);
}

.tech-stat-number {
  font-size: 2.2rem;
  font-weight: 700;
  color: #4f46e5;
  margin-bottom: 5px;
}

.tech-stat-label {
  font-size: 0.9rem;
  color: #e5e7eb;
  opacity: 0.8;
}

.tech-header-scroll {
  position: absolute;
  bottom: -100px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #a5b4fc;
  cursor: pointer;
  animation: bounce 2s infinite;
}

.tech-scroll-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.tech-scroll-icon svg {
  width: 24px;
  height: 24px;
}

.tech-header-scroll span {
  font-size: 0.9rem;
}

/* الأنيميشنات */
@keyframes glowPulse {
  0% { opacity: 0.3; transform: scale(1); }
  100% { opacity: 0.6; transform: scale(1.1); }
}

@keyframes particleFloat {
  0%, 100% { transform: translate(0, 0); }
  25% { transform: translate(10px, -10px); }
  50% { transform: translate(-5px, 5px); }
  75% { transform: translate(5px, -5px); }
}

@keyframes lineGrow {
  from { width: 0; opacity: 0; }
  to { width: 150px; opacity: 1; }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0) translateX(-50%); }
  40% { transform: translateY(-20px) translateX(-50%); }
  60% { transform: translateY(-10px) translateX(-50%); }
}

@media (max-width: 768px) {
  .tech-logo h1 {
    font-size: 2.5rem;
  }
  
  .tech-subtitle {
    font-size: 1.2rem;
  }
  
  .tech-header-stats {
    gap: 20px;
    margin: 40px 0;
  }
  
  .tech-stat {
    padding: 15px 20px;
    min-width: 120px;
  }
  
  .tech-stat-number {
    font-size: 1.8rem;
  }
}
/*  */

.tech-search-container {
  margin: 30px 0;
}

.tech-search-box {
  position: relative;
  max-width: 600px;
  margin: 0 auto;
}

.tech-search-input {
  width: 100%;
  padding: 15px 50px 15px 20px;
  background-color: rgba(17, 17, 27, 0.8);
  border: 1px solid rgba(79, 70, 229, 0.3);
  border-radius: 30px;
  color: #e5e7eb;
  font-size: 1rem;
  outline: none;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.tech-search-input:focus {
  border-color: #4f46e5;
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.3);
}

.tech-search-input::placeholder {
  color: #6b7280;
}

.tech-search-icon {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  color: #6b7280;
  transition: color 0.3s;
}

.tech-search-input:focus + .tech-search-icon {
  color: #4f46e5;
}

.tech-products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
  margin: 40px 0;
}

.tech-product-card {
  position: relative;
  background: linear-gradient(145deg, #11111b 10%, #1e1e2e 100%);
  border-radius: 12px;
  overflow: hidden;
  transition: 0.5s;
  opacity: 0;
  transform: translateY(20px);
  animation: cardEntry 0.6s cubic-bezier(0.22, 1, 0.36, 1) forwards;
  animation-delay: var(--delay);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(79, 70, 229, 0.1);
}

.tech-product-card:hover {
  
  scale: 0.95;
  box-shadow: 0 15px 50px rgba(79, 70, 229, 0.2);
}

.tech-product-image {
  position: relative;
  height: 180px;
  overflow: hidden;
}

.tech-product-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s;
}

.tech-product-card:hover .tech-product-image img {
  transform: scale(1.09);
}

.tech-product-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(17, 17, 27, 0.9) 0%, transparent 50%);
}

.tech-product-content {
  padding: 20px;
  position: relative;
  
  height: 100%;
  z-index: 1;
}

.tech-product-title {
  font-size: 1.4rem;
  margin-bottom: 10px;
  color: #f9fafb;
  font-weight: 600;
}

.tech-product-price {
  color: #4f46e5;
  font-size: 1.3rem;
  font-weight: 700;
  margin: 10px 0;
}

.tech-product-desc {
  color: #9ca3af;
  line-height: 1.6;
  margin-bottom: 20px;
  font-size: 0.95rem;
}

.tech-product-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 12px 20px;
  background: linear-gradient(90deg, #4f46e5 0%, #7c3aed 100%);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: 500;
  overflow: hidden;
  position: relative;
 
}

.tech-product-btn svg {
  width: 15px;
  height: 15px;
  fill: none;
  stroke: white;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  transition: transform 0.3s;
}

.tech-product-btn:hover {
  box-shadow: 0 5px 30px rgba(79, 70, 229, 0.4);
}

.tech-product-btn:hover svg {
  transform: translateX(100px);
}

.tech-product-corner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
  background: linear-gradient(135deg, #0d0b31 0%, transparent 50%);
  border-top-right-radius: 12px;
  border-bottom-left-radius: 12px;
}

.tech-no-results {
  grid-column: 1 / -1;
  text-align: center;
  padding: 60px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tech-no-results-icon {
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
  color: #6b7280;
}

.tech-no-results p {
  font-size: 1.2rem;
  color: #9ca3af;
}


/* الأنيميشنات */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes cardEntry {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes gradientFlow {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* تأثيرات خاصة */
@media (max-width: 768px) {
  .tech-products-grid {
    grid-template-columns: 1fr;
  }
  
  .tech-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .tech-header-info {
    flex-direction: column;
    gap: 5px;
  }
}

/* fff */
.tech-footer {
  position: relative;
 
  color: #e5e7eb;
  padding-top: 80px;
  margin-top: 80px;
}

.tech-footer-waves {
  position: absolute;
  top: -60px;
  left: 0;
  width: 100%;
  height: 60px;
  overflow: hidden;
}

.tech-footer-waves svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #1e1e2e;
}

.tech-footer-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

.tech-footer-columns {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  margin-bottom: 60px;
}

.tech-footer-column {
  position: relative;
}

.tech-footer-title {
  font-size: 1.3rem;
  color: #a5b4fc;
  margin-bottom: 20px;
  position: relative;
  padding-bottom: 10px;
}

.tech-footer-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  width: 50px;
  height: 2px;
  background: linear-gradient(90deg, #4f46e5 0%, transparent 100%);
}

.tech-footer-about {
  color: #9ca3af;
  line-height: 1.6;
  margin-bottom: 20px;
}

.tech-footer-social {
  display: flex;
  gap: 15px;
}

.tech-social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(79, 70, 229, 0.1);
  color: #a5b4fc;
  transition: all 0.3s;
}

.tech-social-link:hover {
  background-color: #4f46e5;
  color: white;
  transform: translateY(-3px);
}

.tech-social-link svg {
  width: 20px;
  height: 20px;
}

.tech-footer-links {
  list-style: none;
}

.tech-footer-links li {
  margin-bottom: 12px;
}

.tech-footer-links a {
  color: #9ca3af;
  text-decoration: none;
  transition: color 0.3s;
  display: inline-block;
  position: relative;
}

.tech-footer-links a::before {
  content: '';
  position: absolute;
  bottom: -2px;
  right: 0;
  width: 0;
  height: 1px;
  background-color: #4f46e5;
  transition: width 0.3s;
}

.tech-footer-links a:hover {
  color: #e5e7eb;
}

.tech-footer-links a:hover::before {
  width: 100%;
  left: 0;
  right: auto;
}

.tech-footer-contact {
  list-style: none;
}

.tech-footer-contact li {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  color: #9ca3af;
}

.tech-footer-contact svg {
  width: 20px;
  height: 20px;
  margin-left: 10px;
  color: #4f46e5;
  flex-shrink: 0;
}

.tech-footer-newsletter {
  color: #9ca3af;
  margin-bottom: 20px;
  line-height: 1.6;
}

.tech-newsletter-form {
  position: relative;
  display: flex;
}

.tech-newsletter-form input {
  width: 100%;
  padding: 12px 20px;
  background-color: rgba(17, 17, 27, 0.8);
  border: 1px solid rgba(79, 70, 229, 0.3);
  border-radius: 30px;
  color: #e5e7eb;
  outline: none;
  transition: all 0.3s;
}

.tech-newsletter-form input:focus {
  border-color: #4f46e5;
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.2);
}

.tech-newsletter-form button {
  position: absolute;
  left: 5px;
  top: 5px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: linear-gradient(90deg, #4f46e5 0%, #7c3aed 100%);
  color: white;
  border: none;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tech-newsletter-form button:hover {
  transform: rotate(20deg) scale(1.1);
}

.tech-newsletter-form button svg {
  width: 16px;
  height: 16px;
}

.tech-footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding: 30px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.tech-footer-copyright {
  color: #6b7280;
  font-size: 0.9rem;
}

.tech-footer-credits {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
}

.tech-footer-text {
  color: #6b7280;
  font-size: 0.85rem;
}

.tech-footer-link {
  color: #a5b4fc;
  font-size: 0.85rem;
  text-decoration: none;
  transition: color 0.3s;
}

.tech-footer-link:hover {
  color: #4f46e5;
  text-decoration: underline;
}

@media (max-width: 768px) {
  .tech-footer-columns {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .tech-footer-bottom {
    flex-direction: column;
    text-align: center;
  }
  
  .tech-footer-credits {
    align-items: center;
  }
}
