.product-details-container {
  display: flex;
  max-width: 1200px;
  margin: 2rem auto;
  padding: 1rem;
  gap: 2rem;
  direction: rtl;
}

.product-gallery {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.main-image-container {
  position: relative;
  /* border: 1px solid #e1e1e1; */
  border-radius: 8px;
  overflow: hidden;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
    background: radial-gradient(circle, rgba(79, 70, 229, 0.4) 20%, rgba(79, 70, 229, 0) 70%);

}

.main-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 10px;
}

.nav-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.nav-arrow:hover {
  background: rgba(0, 0, 0, 0.8);
}

.left-arrow {
  left: 15px;
}

.right-arrow {
  right: 15px;
}

.thumbnail-container {
  display: flex;
  gap: 0.5rem;
  overflow-x: auto;
  padding-bottom: 0.5rem;
}

.thumbnail {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  opacity: 0.7;
  transition: all 0.3s;
}

.thumbnail:hover, .thumbnail.active {
  opacity: 1;
  border-color: #4f46e5;
}

.product-info {
  flex: 1;
  padding: 0 1rem;
}

.product-title {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  color: #fff;
}

.product-brand {
  color: #eee;
  margin-bottom: 1rem;
}

.price-section {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1.5rem 0;
}

.price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #4f46e5;
}

.stock-status {
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
}

.in-stock {
  background-color: #0f502d;
  color: #13da97;
}

.out-of-stock {
  background-color: #742c2c;
  color: #fc2626;
}

.product-description {
  margin: 1.5rem 0;
  line-height: 1.6;
  color: #ccc;
}

.product-specs {
  background: linear-gradient(145deg, #11111b 10%, #100c24 100%);
  padding: 1.5rem;
  border-radius: 8px;
  margin: 1.5rem 0;
}

.product-specs h3 {
  margin-bottom: 1rem;
  color: #fff;
}

.product-specs ul {
  list-style-type: none;
  padding: 0;
}

.product-specs li {
  padding: 0.5rem 0;
  /* border-bottom: 0.5px solid #eee; */
  display: flex;
  justify-content: space-between;
}


.product-specs strong {
  color: #4f46e5;
}

.action-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.add-to-cart, .buy-now {
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
  flex: 1;
}

.add-to-cart {
  background-color: #4f46e5;
  color: white;
}

.add-to-cart:hover {
  background-color: #4338ca;
}

.buy-now {
  background-color: #10b981;
  color: white;
}

.buy-now:hover {
  background-color: #0d9e6e;
}

.loading, .not-found {
  text-align: center;
  padding: 2rem;
  font-size: 1.2rem;
}

/* للشاشات الصغيرة */
@media (max-width: 768px) {
  .product-details-container {
    flex-direction: column;
  }
  
  .main-image-container {
    height: 300px;
  }
  
  .action-buttons {
    flex-direction: column;
  }
}

.contact-methods {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid #ccc;
}

.contact-methods h3 {
  margin-bottom: 1rem;
  color: #ccc;
}

.contact-buttons {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-buttons button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
}

.whatsapp-btn {
  background: linear-gradient(145deg, #1a500d 10%, #100c24 100%);
  color: white;
}

.whatsapp-btn:hover {
  scale: 1.02;
}

.messenger-btn {
  background: linear-gradient(145deg, #0a2a94 10%, #100c24 100%);
  color: white;
}

.messenger-btn:hover {
 scale: 1.02;
}

.telegram-btn {
  background: linear-gradient(145deg, #216297 10%, #100c24 100%);
  color: white;
}

.telegram-btn:hover {
  scale: 1.02;
}

.contact-icon {
  font-size: 1.2rem;
}

@media (max-width: 768px) {
  .contact-buttons {
    flex-direction: column;
  }
}